body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow-x: hidden;
}

.App-header {
	background-image: url(/portfolio/static/media/Black_Paper.cf9bfb03.png);
	background-repeat: repeat-y;
	background-size: 100vw 100vh;
	min-height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.white-paper-moutain {
	width: 100vw;
	height:100vh;
	background-image: url(/portfolio/static/media/Mountain_Paper.87b12434.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center 60%;
	position: relative;
}
.project-content {
	position: relative;
}
.project-content ul {
	list-style: none;
}
.mrd-logo {
	position: absolute;
	top: 5vh;
	left: 37.5vw;
	width: 20vw;
	-webkit-filter: none;
	        filter: none;
}
.trees, .social-link {
	-webkit-filter: drop-shadow(3px 3px 9px rgba(0,0,0,0.3));
	        filter: drop-shadow(3px 3px 9px rgba(0,0,0,0.3));
}
.trees {
	margin: 3vh 0;
	width: 15vw;
}
.left-trees {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	position: absolute;
	top: 25vh;
	width: 50vw;
	left:0;
}
.right-trees {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	position: absolute;
	top: 25vh;
	width: 50vw;
	right: 0;
}
.bottom-area {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}
.social-link-wrap {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 40vw;
}
.social-link {
	width: 30vw;
	margin: 3vh 0;
}
@media only screen and (max-width: 900px) {
	.mrd-logo {
		left: 40vw;
	}
	.social-link {
		width: 30vw;
		margin: 3vh 0;
	}
	.left-trees, .right-trees {
		margin: 3vh 0;
		-webkit-flex-direction: column;
		        flex-direction: column;
		top: 5vh;
	}
	.trees {
		margin: 1vh 0;
		width: 25vw;
	}
}
@media only screen and (min-width: 900px) {
	.project-content {
		margin-top: -50vh;
		margin-bottom: 25vh;
	}
	.mrd-logo {
		width: 13vw;
		left: 43.5vw;
	}
	.left-trees, .right-trees {
		top: 15vh;
	}
	.trees {
		margin: 1vh 0;
		width: 12vw;
	}
}
@media only screen and (min-width: 1200px) {
	.mrd-logo {
		top: 12vh;
		left: 41vw;
		width: 13vw;
	}
	.trees:hover, .social-link:hover {
		-webkit-filter: drop-shadow(4px 4px 15px rgba(0,0,0,0.25));
		        filter: drop-shadow(4px 4px 15px rgba(0,0,0,0.25));
		cursor: pointer;
	}
	.left-trees, .right-trees {
		display: default;
		position: relative;
		top: auto;
		width: auto;
		left: auto;
	}
	.trees {
		position: absolute;
	}
	.tree-1 {
		top: 55vh;
		left: 5vw;
		width: 9vw;
	}
	.tree-2 {
		top: 48vh;
		left: 16vw;
		width: 9vw;
	}
	.tree-3 {
		top: 40vh;
		left: 27vw;
		width: 8vw;
	}
	.tree-4 {
		top: 30vh;
		right: 27vw;
		width: 9vw;
	}
	.tree-5 {
		top: 30vh;
		right: 15vw;
		width: 9vw;
	}
	.tree-6 {
		top: 44vh;
		right: 5vw;
		width: 9vw;
	}
	.social-link {
		width: 16vw;
	}
}
